<template>

    <div class="buttons-container">
        <button class="project-button" v-if="!continueFlag && !importFlag && tabView == '/inputs'"
            @click="importProject('local')">
            {{ $t("continueProject") }}
        </button>
        <button v-if="tabView == '/inputs'" class="project-button" @click="importProject('json')">
            {{ $t("importProject") }}
        </button>
        <button v-if="tabView == '/inputs'" class="project-button" @click="resetProject()">
            {{ $t("resetProject") }}
        </button>
        <button v-if="tabView == '/resultados'" @click="exportObjectAsJSON()" class="project-button">
            {{ $t("exportProject") }}
        </button>
    </div>


</template>

<script>
import { verificarLocal } from "../assets/javascript/globalFunctions"
import saveProjectMixin from "./mixins/saveProject.vue"
import * as XLSX from 'xlsx'

export default {
    name: "vue-project-buttons",
    mixins: [
        saveProjectMixin
    ],
    emits: ["update-view"],
    data() {
        return {
            project: {}
        }
    },
    computed: {
        importFlag() {
            return this.$store.getters.currentImportFlag
        },
        continueFlag() {
            return this.$store.getters.currentContinueFlag
        },
        tabView() {
            return this.$store.getters.currentTabViewAtual
        }
    },
    methods: {
        async importJSONAsObject() {
            try {
                const [fileHandle] = await window.showOpenFilePicker({
                    types: [{
                        description: "Arquivos JSON",
                        accept: { "application/json": [".json"] }
                    }],
                    multiple: false,
                    excludeAcceptAllOption: true // Fora seleo apenas de JSON
                })

                const file = await fileHandle.getFile()

                // Verificao bsica de tamanho (exemplo: 5MB)
                if (file.size > 5 * 1024 * 1024) {
                    throw new Error("Arquivo muito grande (mximo 5MB)")
                }

                const fileContent = await file.text()

                // Validao da estrutura bsica
                const parsed = JSON.parse(fileContent)
                if (!parsed || typeof parsed !== "object") {
                    throw new Error("Formato JSON invlido")
                }

                // Verificao de propriedades essenciais (personalize conforme seu projeto)
                const requiredProps = ["slideresPrimeira", "criteriosLabelPrimeira"]
                requiredProps.forEach(prop => {
                    if (!(prop in parsed)) {
                        throw new Error(`Propriedade obrigatria ausente: ${prop}`)
                    }
                })

                console.log("Importao bem-sucedida:", parsed)
                return parsed

            } catch (error) {
                if (error.name === "AbortError") {
                    console.log("Importao cancelada pelo usurio")
                } else {
                    console.error("Falha na importao:", error)
                    alert(`Erro: ${error.message}`)
                }
                return null
            }
        },
        async exportObjectAsJSON() {
            try {
                await this.saveProject()
                const projectData = JSON.parse(verificarLocal("last_project"))

                // Map field names to Vietnamese
                const fieldMap = {
                    slideresPrimeira: "Thanh trt ln 1",
                    slideresSegunda: "Thanh trt ln 2",
                    criteriosLabelPrimeira: "Tiu ch ln 1",
                    criteriosLabelSegunda: "Tiu ch ln 2",
                    optionsLabelPrimeira: "Ty chn ln 1",
                    optionsLabelSegunda: "Ty chn ln 2",
                    criteriosSimboloPrimeira: "K hiu tiu ch ln 1",
                    criteriosSimboloSegunda: "K hiu tiu ch ln 2",
                    optionsSimboloPrimeira: "K hiu ty chn ln 1",
                    optionsSimboloSegunda: "K hiu ty chn ln 2"
                }

                // Chuyn i d liu sang key ting Vit
                let row = {}
                if (projectData && typeof projectData === 'object') {
                    Object.keys(fieldMap).forEach(key => {
                        row[fieldMap[key]] = projectData[key] !== undefined ? projectData[key] : ''
                    })
                } else {
                    // Nu khng c d liu, to dng mu rng
                    Object.values(fieldMap).forEach(vnKey => {
                        row[vnKey] = ''
                    })
                }

                const worksheet = XLSX.utils.json_to_sheet([row])
                const workbook = XLSX.utils.book_new()
                XLSX.utils.book_append_sheet(workbook, worksheet, "D liu d n")

                const fileName = `du-an-${Date.now()}.xlsx`
                const fileHandle = await window.showSaveFilePicker({
                    suggestedName: fileName,
                    types: [{
                        description: "Tp Excel",
                        accept: { "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet": [".xlsx"] }
                    }]
                })
                const excelBuffer = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' })
                const writable = await fileHandle.createWritable()
                await writable.write(new Blob([excelBuffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' }))
                await writable.close()

                // Lu ln API nh c
                try {
                    const response = await fetch('https://api-ahp.onrender.com/api/projects', {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        body: JSON.stringify({
                            ...projectData,
                            id: Date.now().toString(),
                            timestamp: new Date().toISOString()
                        })
                    })
                    if (!response.ok) {
                        throw new Error('Khng th lu d n ln server')
                    }
                    console.log("D n  c lu ln server thnh cng")
                } catch (apiError) {
                    console.error("Li khi lu ln server:", apiError)
                }
                console.log("Xut file Excel thnh cng:", fileName)
            } catch (error) {
                if (error.name !== "AbortError") {
                    console.error("Erro na exportao:", error)
                    alert(`Erro ao exportar projeto: ${error.message}`)
                }
            }
        },
        async importProject(store) {

            if (store == "json") {
                console.log("Importing from json file...")
                this.project = await this.importJSONAsObject()
            }

            if (store == "local") {
                console.log("Importing from local store")
                this.project = JSON.parse(verificarLocal("last_project"))
            }

            try {
                this.$store.dispatch("changeImportFlag", true)
                Object.keys(this.project).forEach(key => {
                    this.$store.dispatch(`change${key.charAt(0).toUpperCase() + key.slice(1)}`, this.project[key])

                })
                this.$emit("update-view")
            } catch {
                console.log("Projeto importado est vazio ou contm erros")
            }

        },
        resetProject() {
            try {
                Object.keys(this.project).forEach(key => {
                    this.$store.dispatch(`change${key.charAt(0).toUpperCase() + key.slice(1)}`, [])

                })
                window.location.reload()
            } catch {
                console.log("O projeto j foi resetado")
            }
        }
    }

}
</script>
<style>
.buttons-container {
    position: absolute;
    top: 2rem;
    left: 2rem;
    display: flex;
    gap: 1rem;
    z-index: 10;
}

.project-button {
    padding: 0.75rem 1.25rem;
    border-radius: 8px;
    border: 1px solid #000;
    background: white;
    color: #000;
    font-size: 0.9rem;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    display: flex;
    align-items: center;
    gap: 0.5rem;
}

/* Style cho nt Continue Project */
.project-button:nth-child(1) {
    background: #000;
    color: white;
}

.project-button:nth-child(1):hover {
    background: #333;
    transform: translateY(-1px);
}

/* Style cho nt Import Project */
.project-button:nth-child(2) {
    background: white;
}

.project-button:nth-child(2):hover {
    background: #f8f8f8;
    transform: translateY(-1px);
}

/* Style cho nt Reset Project */
.project-button:nth-child(3) {
    border-color: #ff4757;
    color: #ff4757;
    background: white;
}

.project-button:nth-child(3):hover {
    background: #fff5f5;
    transform: translateY(-1px);
}

/* Style cho nt Export Project */
.project-button:nth-child(4) {
    background: white;
    border-color: #000;
}

.project-button:nth-child(4):hover {
    background: #f8f8f8;
    transform: translateY(-1px);
}

/* Active state cho tt c cc nt */
.project-button:active {
    transform: translateY(0);
}

/* Responsive */
@media (max-width: 768px) {
    .buttons-container {
        position: static;
        padding: 1rem;
        flex-wrap: wrap;
        justify-content: center;
    }

    .project-button {
        padding: 0.6rem 1rem;
        font-size: 0.85rem;
    }
}
</style>
